import React from 'react'
class Contact extends React.Component{
    render()
    {
        return (
            <div style={{paddingTop:"5%"}}>
                <div className="container">
                    <div className="row">
                        <div className="col-md-4 offset-md-3">
                            <div className="">
                                <form action="">
                                <div class="form-group">
                                    <label for="email">Name :</label>
                                    <input type="email" placeholder="Enter your name" class="form-control" id="email" />
                                </div>
                                <div class="form-group">
                                    <label for="pwd">Email:</label>
                                    <input type="email" placeholder="Enter your Email" class="form-control" id="pwd" /> 
                                </div>
                                <div>
                                <textarea placeholder="Enter your Message" className="form-control" rows="4">
                                    
                                </textarea>
                                </div>
                                <button type="submit" class="btn btn-success">Submit</button>
                                </form>
                                
                            </div>
                        </div>
                    </div>
                </div>
            
            </div>
        );
    }
}
export default Contact;