import { bindActionCreators } from "redux";
import {normalize} from 'normalizr';
import {authorSchema} from '../schema/authorSchema';
import axios from 'axios';
let request = require("request");
export function flushAuthorState() {
    return dispatch => {
        dispatch({
            type: "FLUSH_AUTHOR",
        })
    }
}
export function increasePerspectiveLike(id) {
    let like = {};
    like.type = "increase";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = true;
                dispatch({
                    type: 'AUTHOR_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function decreasePerspectiveLike(id) {
    let like = {};
    like.type = "decrease";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = false;
                dispatch({
                    type: 'AUTHOR_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function getAuthorInfluencedByAuthors(id) {
    return dispatch => {
        dispatch({
            type: "GET_AUTHOR_AUTHORS_PENDING"
        })
        axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authors/${id}?relationships=true&withSelfAssociationsOnly=true`).then(data => {
            const { responseType } = data.data;
            if (responseType == 'success') {
               // getPictureLinks(data.data.data);
                const author_authors_details =data.data.data;
                dispatch({
                    type: "GET_AUTHOR_AUTHORS_SUCCESS",
                    payload: author_authors_details,
                })
            }
            else {
                dispatch({
                    type: "GET_AUTHOR_AUTHORS_SERVER_ERROR"
                })
            }
        }).catch(error => {
            dispatch({
                type: "GET_AUTHOR_AUTHORS_NETWORK_ERROR"
            })
            console.log('Error While fetching author_author details from api', error);
        })

    }

}

function getPictureLinks(data){
    let author_lastName=(((data.lastName).split(" ")).join("+")).toLowerCase();
    let author_firstName=(((data.firstName).split(" ")).join("+")).toLowerCase();
    author_firstName=((author_firstName).split(".")).join("");
    let author_image=author_lastName.concat("+"+author_firstName+".jpg");
    data.pictureLink=`https://conceptionary-images.s3.amazonaws.com/${author_image}`;
    return data;
}

export function getAuthorDetails(id,offset=0,likes=true) {
    return dispatch => {
        if(offset>0)
            {
                axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authors/${id}/${offset}/${likes}?relationships=true`).then(data => {
                    console.log(data.data);
                    const { responseType } = data.data;
                    if (responseType == 'success') {
                        const authorDetailsFromApi =getPictureLinks( data.data.data);
                        const normalizedData =normalize(authorDetailsFromApi,authorSchema)
                            dispatch({
                                type: "GET_AUTHOR_DETAILS_BY_SCROLL",
                                payload: authorDetailsFromApi,
                            })
                        dispatch({
                            type:'AUTHOR_DETAILS_BY_SCROLL',
                            payload:normalizedData.entities
                        })
                    }
                    else {
                        dispatch({
                            type: "SERVER_ERROR"
                        })
                    }
                }).catch(error => {
                    dispatch({
                        type: "NETWORK_ERROR"
                    })
                    console.log('Error While fetching author details from api', error);
                })
            }
            else{
               
                axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authors/${id}/${offset}/${likes}?relationships=true`).then(data => {
                    const { responseType } = data.data;
                    if (responseType == 'success') {
                        const authorDetailsFromApi =getPictureLinks( data.data.data);
                        const normalizedData =normalize(authorDetailsFromApi,authorSchema)
                        dispatch({
                            type: "GET_AUTHOR_DETAILS",
                            payload: authorDetailsFromApi,
                        })
                        dispatch({
                            type:'AUTHOR_DETAILS',
                            payload:normalizedData.entities
                        })
                    }
                    else {
                        dispatch({
                            type: "SERVER_ERROR"
                        })
                    }
                }).catch(error => {
                    dispatch({
                        type: "NETWORK_ERROR"
                    })
                    console.log('Error While fetching author details from api', error);
                })
            }


        
        }
}