import React, { Component } from 'react';
//import { ShareButton } from "react-custom-share";
import { FacebookProvider, Share } from 'react-facebook';
import ShareLink from 'react-twitter-share-link'

import {
    FacebookIcon,TwitterIcon
} from 'react-share';
import { Helmet } from "react-helmet";
import TwitterSharedButton from './TwitterSharedButton';
class ShareButtons extends Component {
    constructor(props) {
        super(props);
        this.state={
          show:false
        }
    }
    render() {
      
        const { data,title,author } = this.props;
        const link = `http://conceptionary.io/perspective/${data.id}`;
        const tweetLink=" ";
        let text=" ";
        console.log(data.pronoun);
        if(data.pronoun)
        {
          text=data.pronoun.charAt(0).toUpperCase() + data.pronoun.slice(1)+" "+title.charAt(0).toUpperCase() + title.slice(1)+" is "+data.description+"\n- "+author+ "   conceptionary.io";
        }
        else
        text=title.charAt(0).toUpperCase() + title.slice(1)+" is "+data.description+"\n- "+author+ "   conceptionary.io";
        return (
          <div >
            <FacebookProvider appId="930857800581355">
              <Share href={link}>
                {({ handleClick}) => (
                
              <button type="button" onMouseDown={()=>{this.setState({show:true})}} onClick={handleClick} style={{backgroundColor: 'transparent',border: 'none'}}> <FacebookIcon size={32} round={true} /></button>
                )}
              </Share>
            </FacebookProvider>
            <ShareLink link={tweetLink} text={text}>
              {link => (
                <a href={link} style={{display:"inline-block"}} target='_blank'> <TwitterIcon size={32} round={true} /></a>
              )}
          </ShareLink>

         {this.state.show && <Helmet>
            {/* <meta property="og:url" content={`http://conceptionary.io/perspective/${data.id}`} />
            <meta property="og:type" content="website" />
            <meta property="fb:app_id" content="930857800581355" />
            <meta property="og:title" content={title} />
            <meta property="og:description" content={data.description} />
            <meta property="og:image" content={`http://conceptionary.io/images/${data.id}.png`} />
        
          

            <meta name="twitter:card" content="summary_large_image" />
            <meta name="Twitter:site" content="Conceptionary" />
            <meta name="twitter:creator" content="Conceptionary.com" />
            <meta name="twitter:title" content={title} />
            <meta name="twitter:description" content={data.description} />
            <meta name="twitter:image" content={`http://conceptionary.io/images/${data.id}.png`} /> */}

          {/* <meta name="description" content={data.description}/>
          <meta property="og:site_name" content="Conceptionary" />
          <meta property="og:title" content={title} />
          <meta property="og:type" content="article" />
          <meta property="og:url" content={`http://conceptionary.io/perspective/${data.id}`} />
          <meta property="og:description" content={data.description} />
          <meta property="twitter:card" content="summary" />
          <meta property="twitter:title" content={data.title} />
          <meta property="twitter:description" content={data.description} />
          <meta property="twitter:domain" content="http://conceptionary.io" />
          <meta property="og:image" content={`http://conceptionary.io/images/${data.id}.png`} />
          <meta property="og:image:type" content="image/jpeg" />
          <meta property="og:image:width" content="200" />
          <meta property="og:image:height" content="200" />
          <meta property="twitter:image:src" content={`http://conceptionary.io/images/${data.id}.png`} /> */}


        </Helmet>}






          </div>
        );
      }
}

export default ShareButtons;
